      fullStyle: ['/*\n* Inspired by https://www.geekaso.com/\n* \u4F60\u597D\uFF0C\u6211\u662F\u9053\u957F\uFF0C\n* \u4E00\u4E2AASO\u4F18\u5316\u5E08\uFF0C\u534A\u4E2A\u8FD0\u8425\uFF0C\n* \u534A\u4E2A\u7A0B\u5E8F\u5458\uFF0C\u534A\u4E2A\u5206\u6790\u5E08\uFF0C\n* \u975E\u5E38\u6709\u5E78\u80FD\u5728\u8FD9\u91CC\uFF0C\n* \u8DDF\u4F60\u5206\u4EAB\u6211\u7684\u4E2A\u4EBA\u7B80\u4ECB\uFF0C\n*/\n\n/* \u6211\u5148\u7ED9\u6240\u6709\u5143\u7D20\u52A0\u4E0A\u8FC7\u6E21\u6548\u679C */\n* {\n  transition: all .3s;\n}\n/* \u767D\u8272\u80CC\u666F\u597D\u50CF\u592A\u5355\u8C03\u4E86\uFF0C\u6765\u70B9\u80CC\u666F\u5427 */\nhtml {\n  color: rgb(222,222,222);\n  background: rgb(0,43,54);\n}\n/* \u6587\u5B57\u662F\u4E0D\u662F\u79BB\u8FB9\u6846\u592A\u8FD1\u4E86\uFF1F */\n.styleEditor {\n  padding: .5em;\n  border: 1px solid;\n  overflow: auto;\n  width: 90vw;\n  margin: 2.5vh 5vw;\n  height: 90vh;\n}\n/* \u6709\u70B9\u592A\u9AD8\u4E86 */\n.styleEditor {\n  height: 45vh;\n}\n/* \u518D\u505A\u4E2A\u4EE3\u7801\u9AD8\u4EAE\u770B\u770B */\n.token.selector{\n  color: rgb(133,153,0);\n}\n.token.property{\n  color: rgb(187,137,0);\n}\n.token.punctuation{\n  color: yellow;\n}\n.token.function{\n  color: rgb(42,161,152);\n}\n\n/* \u518D\u52A0\u70B9 3D \u6548\u679C\u5457 */\nhtml{\n  perspective: 1000px;\n}\n.styleEditor {\n  position: fixed; left: 0; top: 0;\n  transform: rotateX(-10deg) translateZ(-50px) ;\n}\n\n/* \u63A5\u4E0B\u6765\u6211\u7ED9\u81EA\u5DF1\u51C6\u5907\u4E86\u4E00\u4E2A\u7F16\u8F91\u5668 */\n.resumeEditor{\n  position: fixed;\n  top: 50%; left: 0;\n  padding: .5em;  margin: 2.5vh;\n  width: 95vw; height: 45vh;\n  border: 1px solid;\n  background: white; color: #222;\n  overflow: auto;\n}\n/* \u597D\u4E86\uFF0C\u6211\u5F00\u59CB\u5199\u7B80\u4ECB\u4E86 */\n\n\n', '\n/* \u8FD9\u4E2A\u7B80\u5386\u597D\u50CF\u5DEE\u70B9\u4EC0\u4E48\n * \u5BF9\u4E86\uFF0C\u8FD9\u662F Markdown \u683C\u5F0F\u7684\uFF0C\u6211\u9700\u8981\u53D8\u6210\u5BF9 \u6D4F\u89C8\u8005 \u66F4\u53CB\u597D\u7684\u683C\u5F0F\n * \u7B80\u5355\uFF0C\u7528\u5F00\u6E90\u5DE5\u5177\u7FFB\u8BD1\u6210 HTML \u5C31\u884C\u4E86\n */\n', '\n/* \u518D\u5BF9 HTML \u52A0\u70B9\u6837\u5F0F */\n.resumeEditor{\n  padding: 2em;\n}\n.resumeEditor h2{\n  display: inline-block;\n  border-bottom: 1px solid;\n  margin: 1em 0 .5em;\n}\n.resumeEditor ul,.resumeEditor ol{\n  list-style: none;\n}\n.resumeEditor ul> li::before{\n  content: \'\u2022\';\n  margin-right: .5em;\n}\n.resumeEditor ol {\n  counter-reset: section;\n}\n.resumeEditor ol li::before {\n  counter-increment: section;\n  content: counters(section, ".") " ";\n  margin-right: .5em;\n}\n.resumeEditor blockquote {\n  margin: 1em;\n  padding: .5em;\n  background: #ddd;\n}\n'],
      currentMarkdown: '',
      fullMarkdown: '\u9053\u957F@ASO\u9ED1\u79D1\u6280\n----\n\n\u4E00\u4E2AASO\u4F18\u5316\u5E08\uFF0C\u534A\u4E2A\u8FD0\u8425\uFF0C\n\u534A\u4E2A\u7A0B\u5E8F\u5458\uFF0C\u534A\u4E2A\u5206\u6790\u5E08\u3002\n\n\u4E2A\u4EBA\u535A\u5BA2 [GeekASO](https://www.geekaso.com) \u3002\n\u4E2A\u4EBA\u535A\u5BA2 [ASO\u9ED1\u79D1\u6280](https://www.asoheikeji.com) \u3002\n\n\u804C\u4E1A\u7ECF\u5386\n----\n\n* 2014-2015\uFF0C\u8D35\u5DDE\u94DC\u4EC1\uFF0C\u8DA3\u5206\u671F\u672C\u6821\u5185\u8D1F\u8D23\u4EBA\uFF0C\u66FE\u5E26\u9886\u56E2\u961F\u505A\u5230\u5355\u6708\u5355\u4E2A\u6821\u533A\u4E1A\u7EE9\u5168\u56FDTop3\uFF1B\n* 2015-2016\uFF0C\u6D59\u6C5F\u676D\u5DDE\uFF0C\u5206\u671F\u7BA1\u5BB6\u8FD0\u8425\u5B9E\u4E60\uFF0C\u66FE\u642D\u5EFA100+\u65B0\u5A92\u4F53\u516C\u53F7\u77E9\u9635\uFF0C\u81EA\u7136\u5F15\u6D41\uFF1B\n* 2016-2017\uFF0C\u6D59\u6C5F\u676D\u5DDE\uFF0C\u5206\u671F\u7BA1\u5BB6ASO\u8FD0\u8425\uFF0C\u8D1F\u8D23\u91D1\u878D\u9A6C\u7532\u5305\u4E0A\u67B6\uFF0C\u6295\u653E\uFF0C\u4E8E2017\u5E74\u7387\u5148\u5206\u4EAB[ivp6](https://blog.csdn.net/ShellM/article/details/72621427)\u53CA[4.3\u9A6C\u7532](https://blog.csdn.net/ShellM/article/details/72293253)\u89E3\u51B3\u65B9\u6848\uFF1B\n* 2017-2019\uFF0C\u6D59\u6C5F\u676D\u5DDE\uFF0C\u50B2\u71A0\u7F51\u7EDCSO\u8FD0\u8425\uFF0C\u8D1F\u8D23\u91D1\u878D\u9A6C\u7532\u5305\u4E0A\u67B6\uFF0C\u6295\u653E\u3001\u8D1F\u8D23\u516C\u53F8\u673A\u623F\u642D\u5EFA\u7EF4\u62A4\uFF0C\u6D4B\u8BD5\u6295\u653E\u7B56\u7565\uFF1B\u5E76\u4E8E\u671F\u95F4\u627F\u63A5\u90E8\u5206\u91D1\u878D\u9A6C\u7532\u5305\u4E0A\u67B6\uFF0C\u6295\u653E\u4F18\u5316\u7B49\u670D\u52A1\uFF1B\n* 2019.9-2019.11\uFF0C\u5317\u4EAC\uFF0C\u5DE8\u638C\u6570\u636E\u5206\u6790\u5E08\uFF0C\u8D1F\u8D23ASO\u7814\u7A76\uFF0C\u6570\u636E\u5206\u6790\uFF0C\u516C\u53F8ASO\u6280\u80FD\u57F9\u8BAD\uFF1B\u7814\u7A76\u51FAApp\u9AD8\u8986\u76D6\u7B56\u7565\uFF0C[1000-22000\u8986\u76D6](https://www.qimai.cn/app/rank/appid/1443043163/country/cn)\uFF0C[10000-35000\u8986\u76D6](https://www.qimai.cn/app/keyword/appid/1071402253/country/cn)\uFF1B\n* 2019.11-\uFF0C\u5317\u4EAC\uFF0C\u4E03\u9EA6\u6570\u636E\u5206\u6790\u5E08\uFF0C\u8D1F\u8D23ASO\u7814\u7A76\uFF0C\u6570\u636E\u5206\u6790\uFF0C\u516C\u53F8ASO\u6280\u80FD\u57F9\u8BAD\u3002\n\n\u4E2A\u4EBA\u6280\u80FD\n----\n\n1. ASO\u4F18\u5316\uFF0C\n2. Python\u81EA\u52A8\u5316\uFF0C\n3. Shell\u642C\u7816\u547D\u4EE4\uFF0C\n4. \u6570\u636E\u5206\u6790\uFF0C\n\n\u8BFE\u5916\u5B9E\u8DF5\n----\n\n1. \u4E09\u8282\u8BFE\u589E\u957F\u9ED1\u5BA2P1\u52A9\u6559\n2. \u4E09\u8282\u8BFE\u6570\u636E\u79D1\u5B66P1\u52A9\u6559\n3. \u4E09\u8282\u8BFE\u7535\u5546\u8FD0\u8425\u52A9\u6559\n\n\u5176\u4ED6\n----\n\n* \u5FAE\u4FE1\u5C0F\u7A0B\u5E8F\uFF1A**ASO\u9ED1\u79D1\u6280Pro**\n* \u5FAE\u4FE1\u516C\u4F17\u53F7\uFF1A**ASO\u9ED1\u79D1\u6280**\n\n> \u5982\u679C\u4F60\u559C\u6B22\u8FD9\u4E2A\u7B80\u4ECB\u6548\u679C\uFF0CFork [\u8FD9\u4E2A\u9879\u76EE](https://github.com/jirengu-inc/animating-resume)\u5427\uFF0C\u4E5F\u53EF\u4EE5\u6253\u9020\u4F60\u81EA\u5DF1\u7684\u4E2A\u4EBA\u7B80\u4ECB\uFF01\n\n'
